import treeService from '../services/tree.js';
import froca from "../services/froca.js";
import contextMenu from "./context_menu.js";
import dialogService from "../services/dialog.js";
import server from "../services/server.js";

export default class LauncherContextMenu {
    /**
     * @param {NoteTreeWidget} treeWidget
     * @param {FancytreeNode} node
     */
    constructor(treeWidget, node) {
        this.treeWidget = treeWidget;
        this.node = node;
    }

    async show(e) {
        contextMenu.show({
            x: e.pageX,
            y: e.pageY,
            items: await this.getMenuItems(),
            selectMenuItemHandler: (item, e) => this.selectMenuItemHandler(item)
        })
    }

    async getMenuItems() {
        const note = await froca.getNote(this.node.data.noteId);
        const parentNoteId = this.node.getParent().data.noteId;

        const isVisibleRoot = note.noteId === '_lbVisibleLaunchers';
        const isAvailableRoot = note.noteId === '_lbAvailableLaunchers';
        const isVisibleItem = parentNoteId === '_lbVisibleLaunchers';
        const isAvailableItem = parentNoteId === '_lbAvailableLaunchers';
        const isItem = isVisibleItem || isAvailableItem;
        const canBeDeleted = !note.noteId.startsWith("_"); // fixed notes can't be deleted
        const canBeReset = !canBeDeleted && note.isLaunchBarConfig();;

        return [
            (isVisibleRoot || isAvailableRoot) ? { title: '添加笔记启动器', command: 'addNoteLauncher', uiIcon: "bx bx-plus" } : null,
            (isVisibleRoot || isAvailableRoot) ? { title: '添加脚本启动器', command: 'addScriptLauncher', uiIcon: "bx bx-plus" } : null,
            (isVisibleRoot || isAvailableRoot) ? { title: '添加自定义部件', command: 'addWidgetLauncher', uiIcon: "bx bx-plus" } : null,
            (isVisibleRoot || isAvailableRoot) ? { title: '添加间隔', command: 'addSpacerLauncher', uiIcon: "bx bx-plus" } : null,
            (isVisibleRoot || isAvailableRoot) ? { title: "----" } : null,
            { title: '删除<kbd data-command="deleteNotes"></kbd>', command: "deleteNotes", uiIcon: "bx bx-trash", enabled: canBeDeleted },
            { title: '重置', command: "resetLauncher", uiIcon: "bx bx-empty", enabled: canBeReset},
            { title: "----" },
            isAvailableItem ? { title: '移动到可见启动器', command: "moveLauncherToVisible", uiIcon: "bx bx-show", enabled: true } : null,
            isVisibleItem ? { title: '移动到可用启动器', command: "moveLauncherToAvailable", uiIcon: "bx bx-hide", enabled: true } : null,
            { title: `复制启动器 <kbd data-command="duplicateSubtree">`, command: "duplicateSubtree", uiIcon: "bx bx-empty",
                enabled: isItem }
        ].filter(row => row !== null);
    }

    async selectMenuItemHandler({command}) {
        if (command === 'resetLauncher') {
            const confirmed = await dialogService.confirm(`您确定要重置"${this.node.title}"吗? 这个笔记和它的子笔记的所有数据都会丢失, 并且启动器会恢复到它原来的位置.`);

            if (confirmed) {
                await server.post(`special-notes/launchers/${this.node.data.noteId}/reset`);
            }

            return;
        }

        this.treeWidget.triggerCommand(command, {
            node: this.node,
            notePath: treeService.getNotePath(this.node),
            selectedOrActiveBranchIds: this.treeWidget.getSelectedOrActiveBranchIds(this.node),
            selectedOrActiveNoteIds: this.treeWidget.getSelectedOrActiveNoteIds(this.node)
        });
    }
}
